import React from "react"
import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import TextField from '@material-ui/core/TextField'
import InputAdornment from '@material-ui/core/InputAdornment'
import Button from '@material-ui/core/Button'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import CardMedia from '@material-ui/core/CardMedia'
import i18n from "i18next"
import { fetchLoans } from "./Loan.actions"
import "./Loan.scss"

class Loan extends React.Component {
	static propTypes = {
		loading: PropTypes.bool.isRequired,
		items: PropTypes.array.isRequired,
		dispatch: PropTypes.func.isRequired
	}
	constructor(props) {
		super(props)
		this.state = {
			fields: {
				name: "",
				address: "",
				income: "",
				amount: ""
			}
		}
		this.handleChange = this.handleChange.bind(this)
    	this.handleSubmit = this.handleSubmit.bind(this)
	}
	componentWillMount() {
		this.props.dispatch(fetchLoans())
	}
	handleChange = prop => event => {
		this.setState({ 
			fields: {
				[prop]: event.target.value 
			}
		})
	}
	handleSubmit(event) {
		alert('A name was submitted: ' + this.state.value)
		event.preventDefault()
	}
	render () {
		return (
			<div className="loan">
				<Card>
					<CardMedia
						component="img"
						alt="Contemplative Reptile"
						height="200"
						image="/img/renew-unsplash.jpg"
						title="Renew Financial Loan Application"
					/>
					<CardContent>
						<form>
							<h2>{i18n.t("loan.form.title")}</h2>
							<TextField 
								className="field"
								name="name" 
								label={i18n.t("loan.fields.name.label")}
								value={this.state.fields.name}
								margin="normal"
								variant="outlined"
								onChange={this.handleChange('name')}
								/>
							<TextField 
								className="field"
								name="address" 
								label={i18n.t("loan.fields.address.label")}
								value={this.state.fields.address}
								margin="normal"
								variant="outlined"
								onChange={this.handleChange('address')}
							/>
							<TextField 
								className="field"
								name="income" 
								label={i18n.t("loan.fields.income.label")}
								value={this.state.fields.income}
								margin="normal"
								variant="outlined"
								onChange={this.handleChange('income')}
								InputProps={{
									startAdornment: <InputAdornment position="start">$</InputAdornment>,
								  }}
							/>
							<TextField 
								className="field"
								name="amount" 
								label={i18n.t("loan.fields.amount.label")}
								value={this.state.fields.amount}
								margin="normal"
								variant="outlined"
								onChange={this.handleChange('amount')}
								InputProps={{
									startAdornment: <InputAdornment position="start">$</InputAdornment>,
								  }}
							/>
							<div className="row">
								<Button variant="contained" color="primary" size="large">
									{i18n.t("loan.form.submit")}
								</Button>
							</div>
						</form>
					</CardContent>
				</Card>
			</div>
		)
	}
}

const mapStateToProps = state => {
	const { loading, items } = state
	return {
		items,
		loading
	}
}
export default connect(mapStateToProps)(Loan)