import { api } from "@/utils"
//namespace 
export const namespace = "loan"

//actions
export const REQUEST_LOANS = "REQUEST_LOANS"
export const RECEIVE_LOANS = "RECEIVE_LOANS"
export const ADD_LOAN = "ADD_LOAN"

export const loansUrl = "/api/loans"

const requestLoans = ({
	type: REQUEST_LOANS,
	loading: true
})

const receiveLoans = (response) => ({
	type: RECEIVE_LOANS,
	items: response,
	loading: false,
	receivedAt: Date.now()
})

export const fetchLoans = dispatch => {
	dispatch(requestLoans())
	return api.get(loansUrl)
		.then(response => dispatch(receiveLoans(response)))
}
