import {
	REQUEST_LOANS,
	RECEIVE_LOANS,
	ADD_LOANS
} from "./Loan.actions"

const loans = (state = {
	loading: false,
	items: []
}, action) => {
	switch (action.type) {
		case REQUEST_LOANS:
			return {
				...state,
				loading: true
			}
		case RECEIVE_LOANS:
			return {
				...state,
				loading: false,
				items: action.items,
				lastUpdated: action.receivedAt
			}
		default:
			return state
	}
}

export default {
	loans
}